<template>
  <div class="home">
    <!-- Popup -->
    <Popup ref="popup" />

    <div class="row wrapper">
      <div class="left">
        <!-- Navbar -->
        <Navbar
          :walletAddress="walletAddress"
          @logout="
            (walletAddress = 0),
              showPopup(
                'Logout requested',
                'Please go to your wallet and disconnect it from this DAPP',
                5000
              )
          "
        />

        <!-- Mint text -->
        <div class="content">
          <h2 class="mint">mint <span>and</span> sell</h2>
          <h2 class="crypto">crypto</h2>
          <p class="buds">buds</p>
          <div class="mintNow">
            <h3 class="text">mint now</h3>
            <img src="@/assets/arrow.png" alt="" class="arrow" />
            <img src="@/assets/arrowdown.png" alt="" class="arrowdown" />
          </div>
        </div>
      </div>

      <div class="right">
        <!-- Blur pixels -->
        <div class="blur-box">
          <img src="@/assets/rightPixel.png" alt="" />
          <div v-for="i in 9" :key="i" class="box" :class="`box${i}`"></div>
        </div>

        <!-- Progress indicator  -->
        <ProgressComp :current="nfts['minted']" :total="nfts['maxMintable']" />

        <!-- Mint numbers -->
        <div class="values-wrapper">
          <div class="h-line"></div>
          <div class="values">
            <h3>{{ nfts["minted"] }} of {{ nfts["maxMintable"] }}</h3>
            <p>minted</p>
          </div>
          <div class="h-line"></div>
        </div>

        <!-- Mint amount buttons -->
        <div class="range-wrapper">
          <button class="btn dercrementBtn" @click="changeNftsToMint(-1)">
            <p class="minus">-</p>
          </button>
          <input
            type="number"
            :min="nfts['minPerTx']"
            :max="nfts['maxPerTx']"
            @change="changeNftsToMint(0)"
            class="valueInput"
            v-model="nfts['toMint']"
          />
          <span class="maxBtn" @click="nfts['toMint'] = nfts['maxPerTx']"
            >Max</span
          >
          <button class="btn incrementBtn" @click="changeNftsToMint(1)">
            <p>+</p>
          </button>
        </div>

        <!-- Mint button -->
        <button
          class="mintBtn"
          @click="mintNFTsWithEth"
          :class="mintPaused === true ? 'disableMint' : ''"
        >
          mint
        </button>

        <!-- Mint price -->
        <div class="currencies">
          <p class="firstCurrency">1 NFT</p>
          <p class="secondCurrency">
            {{ Math.round(walletDiscountedPriceE15 / 10 ** 3) }} {{ ETH }}
          </p>
        </div>
      </div>
    </div>
  </div>
</template>


<script>
import { ethers } from "ethers";
import KimonNFT from "../../artifacts/contracts/KimonNFT.sol/KimonNFT.json";

import ProgressComp from "../components/ProgressComp.vue";
import Navbar from "../components/Navbar.vue";
import Popup from "../components/Popup.vue";

import { useRoute } from "vue-router";

export default {
  components: { ProgressComp, Navbar, Popup },

  data() {
    return {
      provider: null,
      signer: null,

      walletAddress: 0,
      walletEthBalanceE0: 0,
      ETH: window.ETH,
      discountCode: 0,

      contract: {},
      mintPaused: true,
      ethPerNftE15: 75000, // 75 wDoge
      walletDiscountedPriceE15: 75000, // set the same as ethPerNftE15 in the beginning
      gasPerMint: 220000,

      nfts: {
        toMint: 10,
        minPerTx: 1,
        maxPerTx: 40,

        minted: 0,
        preminted: 10, // counter shows 0 for numbers up to this one
        maxMintable: 1000,
        maxSupply: 10000,
      },
    };
  },

  methods: {
    changeNftsToMint: function (_change) {
      let _newNftsToMint = Math.round(this.nfts["toMint"]) + _change;
      if (_newNftsToMint < this.nfts["minPerTx"]) {
        _newNftsToMint = this.nfts["minPerTx"];
      } else if (_newNftsToMint > this.nfts["maxPerTx"]) {
        _newNftsToMint = this.nfts["maxPerTx"];
      }
      this.nfts["toMint"] = _newNftsToMint;
    },

    showPopup: function (_title, _text, _timeOut) {
      this.$refs.popup.show(_title, _text, _timeOut);
    },

    getEthBalance: function () {
      this.provider.getBalance(this.walletAddress).then((balance) => {
        this.walletEthBalanceE0 = balance;
      });
    },

    mintNFTsWithEth: async function () {
      await this.load(); // get most recent values

      const _nftsToMint = this.nfts["toMint"];

      // check if there are enough NFTs left to mint as the user wants
      let _nftsLeft = this.nfts["maxMintable"] - this.nfts["minted"];
      if (_nftsToMint > _nftsLeft) {
        this.showPopup(
          "Not enough NFTs left",
          `Only ${_nftsLeft} NFTs can still be minted!`,
          3000
        );
        this.nfts["maxPerTx"] = _nftsLeft;
        return false;
      }

      // check if the user has enough funds
      let _totalCostEthE15 = this.walletDiscountedPriceE15 * _nftsToMint;
      const _gasPerMint = window.gasPerMint;
      let _totalGasLimit = Math.min(
        window.maxGasPerTx,
        _gasPerMint +
          (_gasPerMint - 20000) * (_nftsToMint - 1) -
          10000 * Math.floor(_nftsToMint / 7) -
          15000 * Math.floor(_nftsToMint / 10)
      );

      console.log("totalCostEthE15: ", _totalCostEthE15);
      console.log("_totalGasLimit: ", _totalGasLimit);
      console.log(
        "_totalGasCostE15: ",
        (_totalGasLimit * window.minGasCostE9) / 10 ** 6
      );
      console.log("walletEthBalanceE15: ", this.walletEthBalanceE0 / 10 ** 15);

      let _costInclGasE15 =
        _totalCostEthE15 + (_totalGasLimit * window.minGasCostE9) / 10 ** 6;

      if (this.walletEthBalanceE0 / 10 ** 15 < _costInclGasE15) {
        this.showPopup(
          `You have ${
            Math.round(this.walletEthBalanceE0 / 10 ** 14) / 10 ** 4
          } ${this.ETH}.`,
          `You can mint up to ${Math.floor(
            this.walletEthBalanceE0 /
              10 ** 15 /
              (this.walletDiscountedPriceE15 +
                (this.gasPerMint * window.minGasCostE9) / 10 ** 6)
          )} NFTs with that.`,
          3000
        );
        return false;
      }

      // mint NFTs
      this.showPopup(
        `Minting ${_nftsToMint} NFTs.`,
        "Please confirm the transaction in your wallet",
        0
      );

      let tx;
      try {
        tx = await this.contract["nft"].mint(_nftsToMint, {
          value: ethers.utils.parseUnits(_totalCostEthE15.toString(), 15),
          gasLimit: _totalGasLimit.toString(),
        });
        // gasPrice: 1046740000,
        console.log(tx);
        await tx.wait();
        console.log(tx);

        this.load();
      } catch (err) {
        this.showPopup("An error occured.", "Please try again later.", 3000);
        return false;
      }

      this.showPopup("TRANSACTION SENT...", "PLEASE WAIT FOR CONFIRMATION", 0);
      console.log("tx hash:", tx.hash);
      const _txInfo = await this.provider.getTransaction(tx.hash);
      try {
        let code = await this.provider.call(_txInfo, _txInfo.blockNumber);
        console.log(code);
        if (code == "0x") {
          this.showPopup(
            `Congratulations!`,
            `You successfully minted ${_nftsToMint} NFTs!`,
            3000
          );
        }
      } catch (err) {
        const _code = err.data.replace("Reverted ", "");
        console.log({ err });
        let _reason = ethers.utils.toUtf8String("0x" + _code.substr(138));
        console.log("Execution reverted", _reason);
        // this.showPopup("Execution reverted", _reason, 3000);
      }
    },

    load: async function () {
      const that = this;

      that.signer.getAddress().then((_address) => {
        console.log("_address", _address);
        that.walletAddress = _address;

        // that.$refs.navbar.load(_address);

        that.getEthBalance();
        // that.getRewardTokenBalance();
        // that.getFlexUsdBalance();
        // that.contract['nft'].balanceOf(_address).then((_nftsOwned) => {
        //   that.nfts["owned"] = _nftsOwned;
        // });

        that.contract["nft"].getInfo(_address).then((_result) => {
          that.mintPaused = _result[0] === 0 ? false : true;
          that.nfts["minted"] =
            _result[1] > that.nfts["preminted"] ? _result[1] : 0;
          that.nfts["maxMintable"] = _result[2];
          that.nfts["maxSupply"] = _result[3];
          that.nfts["maxPerTx"] = _result[4];

          that.ethPerNftE15 = _result[5];
          let _discountCode = _result[6];

          if (_discountCode > 0) {
            // 1 - n equivalent to 1 - n discounts left
            let _discountsLeft = _discountCode;
            if (that.nfts["maxPerTx"] > _discountsLeft) {
              that.nfts["maxPerTx"] = _discountsLeft;
              that.nfts["toMint"] = _discountsLeft;
            }
            that.walletDiscountedPriceE15 = 1000;
          }
        });
      });
    },

    setupContracts: function () {
      this.contract["nft"] = new ethers.Contract(
        window.address["nft"],
        KimonNFT.abi,
        this.signer
      );
    },
  },

  mounted: function () {
    const route = useRoute();
    this.discountCode = route.query.d;

    const that = this;

    that.provider = new ethers.providers.Web3Provider(window.ethereum, "any");

    that.provider
      .send("eth_requestAccounts", [])
      .then(() => {
        that.signer = that.provider.getSigner();
      })
      .then(() => {
        that.setupContracts();

        that.load();
      });
  },
};
</script>

