<template>
  <!-- search bar -->
  <label for="idInput" class="searchInput">
    <input
      type="text"
      :value="modelValue"
      @input="$emit('update:modelValue', $event.target.value)"
      @click="onClick()"
      @change="onChange()"
      @blur="onBlur()"
      id="idInput"
    />
    <button class="searchButton" @click="searchNfts()">
      <svg
        width="33"
        height="25"
        viewBox="0 0 33 25"
        fill="none"
        xmlns="http://www.w3.org/2000/svg"
      >
        <circle cx="12" cy="12" r="9.5" stroke="#b9b592" stroke-width="3" />
        <line
          x1="21.0125"
          y1="18.3292"
          x2="27.3293"
          y2="21.4876"
          stroke="#b9b592"
          stroke-width="3"
          stroke-linecap="round"
        />
      </svg>
    </button>
  </label>
</template>

<script>
export default {
  // modelValue, update:modelValue, value get() and set() are needed to make this component work with VueJS3 v-model
  // Don't change the names as it will break the v-model functionality
  props: ["modelValue"],
  emits: ["update:modelValue", "search", "NaN"],

  computed: {
    value: {
      get() {
        return this.modelValue;
      },
      set(value) {
        this.$emit("update:modelValue", value);
      },
    },
  },

  methods: {
    onClick: function () {
      if (this.value === "SEARCH FOR #ID") {
        // console.log("onClick triggered");
        this.value = "";
      }
    },

    onBlur: function () {
      if (this.value === "") {
        // console.log("onBlur triggered");
        this.value = "SEARCH FOR #ID";
      }
    },

    onChange: function () {
      // console.log("onChange", this.value);
      if (this.value === "") {
        // console.log("onChange reset");
        this.value = "SEARCH FOR #ID";
        return true;
      }
      if (this.value === "SEARCH FOR #ID") {
        return true;
      }

      if (!Number.isInteger(Number(this.value))) {
        // Number(this.value) is super important
        // Otherwise it will always return false

        // console.log("onChange not int");
        this.value = "SEARCH FOR #ID";
        return;
      }

      let _number = Math.floor(Number(this.value));
      // console.log("onChange", _number);
      if (_number > window.nftsInCollection) {
        _number = window.nftsInCollection;
      } else if (_number < 1) {
        _number = 1;
      }
      if (_number !== this.value) {
        this.value = _number;
      }
    },

    searchNfts: function () {
      let _number = Number(this.value);
      // console.log("searchNfts", _number);

      if (Number.isInteger(_number)) {
        this.$emit("search");
      } else {
        this.$emit("NaN");
      }
    },
  },
};
</script>

