<template>
  <div class="container">
    <Popup ref="popup" />

    <NftPopup ref="nftPopup" />

    <div class="row wrapper">
      <!-- closes dropdown at click -->
      <div
        class="overlay"
        v-if="
          showOptions['sorting'] ||
          showOptions['nftsPerPage'] ||
          showOptions['page']
        "
        @click="
          (showOptions['sorting'] = false),
            (showOptions['nftsPerPage'] = false),
            (showOptions['page'] = false)
        "
      ></div>

      <!-- left -->
      <div class="left">
        <Navbar
          :walletAddress="walletAddress"
          @logout="
            (walletAddress = 0),
              showPopup(
                'Logout requested',
                'Please go to your wallet and disconnect it from this DAPP',
                5000
              )
          "
        />

        <div class="content">
          <h2 class="the">the</h2>
          <p class="gallery">GALLERY</p>
        </div>
      </div>

      <!-- right -->
      <div class="right">
        <div @click="showSortingOptions = false" class="filterBar">
          <!-- search bar -->
          <Searchbar
            v-model.lazy="searchString"
            @search="searchNfts"
            @NaN="
              showPopup(
                'Invalid input',
                'only integer numbers are accepted',
                3000
              )
            "
          />

          <!-- pagination 1/2: nftsPerPage -->
          <div
            class="sortingWrapper"
            :class="showOptions['nftsPerPage'] ? '' : 'hideOptions'"
          >
            <div
              class="sorting"
              @click.stop="
                showOptions['nftsPerPage'] = !showOptions['nftsPerPage']
              "
            >
              {{ nftsPerPage }} per page
            </div>
            <ul class="options" v-if="showOptions['nftsPerPage']">
              <li
                class="option"
                v-for="(_option, _index) in nftsPerPageOptions"
                :key="_index"
                @click="
                  (showOptions['nftsPerPage'] = false),
                    (nftsPerPage = _option),
                    createPageOptions(_option),
                    showNfts(0)
                "
              >
                {{ _option }}
              </li>
            </ul>
          </div>

          <!-- pagination 2/2: page selector -->
          <div
            class="sortingWrapper"
            :class="showOptions['page'] ? '' : 'hideOptions'"
          >
            <div class="pagination">
              <!-- Previous page -->
              <div class="minusBtn" @click="page > 0 ? showNfts(--page) : ''">
                <span>-</span>
              </div>

              <!-- Selector button -->
              <span @click="showOptions['page'] = !showOptions['page']">
                {{ pageOptions[page] }}</span
              >

              <!-- Next page -->
              <div
                class="addBtn"
                @click="page < pageOptions.length - 1 ? showNfts(++page) : ''"
              >
                <span>+</span>
              </div>
            </div>
            <ul class="options" v-if="showOptions['page']">
              <li
                class="option"
                v-for="(_option, _index) in pageOptions"
                :key="_index"
                @click="
                  (showOptions['page'] = false),
                    (page = _index),
                    showNfts(_index)
                "
              >
                {{ _option }}
              </li>
            </ul>
          </div>

          <!-- sorting -->
          <div
            class="sortingWrapper"
            :class="showOptions['sorting'] ? '' : 'hideOptions'"
          >
            <div
              class="sorting"
              @click.stop="showOptions['sorting'] = !showOptions['sorting']"
            >
              {{ sorting }}
            </div>
            <ul class="options" v-if="showOptions['sorting']">
              <li
                class="option"
                v-for="(_option, _index) in sortingOptions"
                :key="_index"
                @click="
                  (showOptions['sorting'] = false),
                    (sorting = _option),
                    sortNfts()
                "
              >
                {{ _option }}
              </li>
            </ul>
          </div>

          <div class="blur-box">
            <img src="@/assets/filterPixel.png" alt="" />
          </div>
        </div>

        <!-- cards -->
        <div class="cards">
          <h2 class="no-nft" v-if="loading">Loading. Please wait.</h2>

          <NftCard
            v-else
            v-for="_tokenId in nfts"
            :key="_tokenId"
            :tokenId="_tokenId"
            :nftInfo="nftInfo[_tokenId]"
            @click="$refs.nftPopup.show(_tokenId, nftInfo[_tokenId])"
          />
          <h2 class="no-nft" v-if="nfts.length <= 0 && !loading">
            No NFTs to display.
          </h2>
        </div>
      </div>
    </div>
  </div>
</template>
  
  <script>
import { ethers } from "ethers";

import Navbar from "../components/Navbar.vue";
import NftCard from "@/components/NftCard.vue";
import NftPopup from "@/components/NftPopup.vue";
import Popup from "../components/Popup.vue";
import Searchbar from "../components/Searchbar.vue";

export default {
  components: { Navbar, NftCard, NftPopup, Popup, Searchbar, Searchbar },

  data() {
    return {
      walletAddress: 0,

      showOptions: {},

      sortingOptions: [
        "unsorted",
        "rank Lo to Hi",
        "rank Hi to Lo",
        "number Lo to Hi",
        "number Hi to Lo",
      ],
      sorting: "unsorted",

      nftsPerPageOptions: [25, 50, 100, 250, 500],
      nftsPerPage: 25,
      pageOptions: ["1-25"],
      page: 0,

      loading: false,
      nfts: [],
      nftInfo: [],
      searchString: "SEARCH FOR #ID",
    };
  },

  methods: {
    showPopup: function (_title, _text, _timeOut) {
      this.$refs.popup.show(_title, _text, _timeOut);
    },

    createPageOptions(_nftsPerPage) {
      this.nftsPerPage = _nftsPerPage;

      this.pageOptions = [];
      for (let i = 1; i < window.nftsInCollection; i += _nftsPerPage) {
        this.pageOptions.push(
          String(i) +
            "-" +
            String(Math.min(i + _nftsPerPage - 1, window.nftsInCollection))
        );
      }
      this.page = 0;
    },

    searchNfts: function () {
      let _tokenId = this.searchString;
      if (this.nfts.includes(_tokenId)) {
        this.showPopup(`${_tokenId} is already in the list`, "", 3000);
        return;
      } else {
        this.showPopup(`Added ${_tokenId} to the list`, "", 3000);
      }

      this.nfts.unshift(_tokenId);

      // let _rarity = that.getRarity(_tokenId);
      let _rank = this.getRank(_tokenId);
      this.nftInfo[_tokenId] = {
        rank: _rank,
        image: window.pictureLink + _tokenId + ".png",
        // rarity: _rarity,
      };

      this.sortNfts();
    },

    showNfts() {
      this.loading = true;

      const _page = this.page;
      this.nfts = [];

      for (
        let _tokenId = _page * this.nftsPerPage + 1;
        _tokenId <= (_page + 1) * this.nftsPerPage;
        ++_tokenId
      ) {
        this.nfts.push(_tokenId);

        // let _rarity = that.getRarity(_tokenId);
        let _rank = this.getRank(_tokenId);
        this.nftInfo[_tokenId] = {
          rank: _rank,
          image: window.pictureLink + _tokenId + ".png",
          // rarity: _rarity,
        };
      }

      this.sortNfts();
      this.loading = false;
    },

    sortNfts: function () {
      let that = this;

      switch (that.sorting) {
        case that.sortingOptions[0]:
          console.log("No sorting required");
          return false;

        case that.sortingOptions[1]:
          that.nfts.sort(function (a, b) {
            return that.nftInfo[a]["rank"] - that.nftInfo[b]["rank"] || a - b;
          });
          break;

        case that.sortingOptions[2]:
          that.nfts.sort(function (a, b) {
            return that.nftInfo[b]["rank"] - that.nftInfo[a]["rank"] || b - a;
          });
          break;

        case that.sortingOptions[3]:
          that.nfts.sort(function (a, b) {
            return a - b;
          });
          break;

        case that.sortingOptions[4]:
          that.nfts.sort(function (a, b) {
            return b - a;
          });
          break;

        default:
          that.feedbackMessage = "Sorting not implemented";
      }
    },

    // getRarity: function (_tokenId) {
    //   return window.rarity[tokenId - 1];
    // },

    getRank: function (_tokenId) {
      return window.rank[_tokenId - 1];
    },

    load: function () {
      this.createPageOptions(this.nftsPerPage);

      this.showNfts(0);

      this.signer.getAddress().then((_address) => {
        console.log("_address", _address);
        this.walletAddress = _address;
      });
    },
  },

  mounted: function () {
    const that = this;

    that.provider = new ethers.providers.Web3Provider(window.ethereum, "any");

    that.provider
      .send("eth_requestAccounts", [])
      .then(() => {
        that.signer = that.provider.getSigner();
      })
      .then(() => {
        try {
          that.signer = that.provider.getSigner();
          that.load();
        } catch {
          this.createPageOptions(this.nftsPerPage);

          this.showNfts(0);
        }
      })
      .catch(() => {
        this.createPageOptions(this.nftsPerPage);

        this.showNfts(0);
      });
  },
};
</script>
  
<style lang="scss" scoped>
@import "@/assets/style.scss";
</style>