<template>
  <div class="progress">
    <svg width="350" height="350" viewBox="0 0 200 200">
      <circle
        cx="100"
        cy="100"
        r="74"
        fill="none"
        stroke-width="12"
        class="inner-circle-unfilled"
      />
      <circle
        cx="100"
        cy="100"
        r="74"
        fill="none"
        stroke-width="12"
        stroke-dasharray="465"
        :stroke-dashoffset="obj.dashOffsetCircle"
        stroke-linecap="round"
        class="inner-circle-filled"
      />
    </svg>
    <div class="percentage">{{ obj.percentage }}%</div>
  </div>
</template>

<script>
export default {
  props: ["current", "total"],

  computed: {
    obj() {
      const _total = this.total <= 0 ? 1 : this.total;
      const _pct = Math.ceil((this.current / _total) * 100);
      const _temp = 4.65 * _pct;
      const _dashOffsetCircle = 465 - _temp;
      return { percentage: _pct, dashOffsetCircle: _dashOffsetCircle };
    },
  },
};
</script>

