<template>
  <div class="container">
    <Popup ref="popup" />

    <NftPopup ref="nftPopup" />

    <div class="row wrapper">
      <!-- closes dropdown at click -->
      <div
        class="overlay"
        v-if="showSortingOptions"
        @click="showSortingOptions = false"
      ></div>

      <!-- left -->
      <div class="left">
        <Navbar
          :walletAddress="walletAddress"
          @logout="
            (walletAddress = 0),
              showPopup(
                'Logout requested',
                'Please go to your wallet and disconnect it from this DAPP',
                5000
              )
          "
        />

        <div class="content">
          <h2 class="the">my</h2>
          <p class="gallery">nfts</p>
        </div>
      </div>

      <!-- right -->
      <div class="right">
        <div @click="showSortingOptions = false" class="filterBar">
          <!-- search bar -->
          <Searchbar
            style="width: 60%"
            v-model.lazy="searchString"
            @search="searchNfts"
            @NaN="
              showPopup(
                'Invalid input',
                'only integer numbers are accepted',
                3000
              )
            "
          />

          <!-- sorting & show all NFTs in wallet-->
          <div
            class="sortingWrapper"
            :class="showSortingOptions ? '' : 'hideOptions'"
          >
            <div
              class="sorting"
              @click.stop="showSortingOptions = !showSortingOptions"
            >
              {{ sorting }}
            </div>

            <ul class="options" v-if="showSortingOptions">
              <li
                class="option"
                v-for="(_option, _index) in sortingOptions"
                :key="_index"
                @click="
                  (showSortingOptions = false), (sorting = _option), sortNfts()
                "
              >
                {{ _option }}
              </li>
            </ul>
          </div>

          <div class="blur-box">
            <img src="@/assets/filterPixel.png" alt="" />
          </div>
        </div>

        <!-- cards -->
        <div class="cards">
          <NftCard
            v-for="_tokenId in nftsToShow"
            :key="_tokenId"
            :tokenId="_tokenId"
            :nftInfo="nftInfo[_tokenId]"
            @click="$refs.nftPopup.show(_tokenId, nftInfo[_tokenId])"
          />

          <div
            id="showMore"
            v-if="nftsToShow.length < nftsInWallet.length"
          ></div>

          <h2 class="no-nft" v-if="loading === true">Loading. Please wait.</h2>

          <h2
            class="no-nft"
            v-if="loading === false && nftsInWallet.length <= 0"
          >
            You have no NFTs in your wallet.
          </h2>
        </div>
      </div>
    </div>
  </div>
</template>
  
  <script>
import { ethers } from "ethers";
import KimonNFT from "../../artifacts/contracts/KimonNFT.sol/KimonNFT.json";

import Navbar from "../components/Navbar.vue";
import NftCard from "@/components/NftCard.vue";
import NftPopup from "@/components/NftPopup.vue";
import Popup from "../components/Popup.vue";
import Searchbar from "../components/Searchbar.vue";

export default {
  components: { Navbar, NftCard, NftPopup, Popup, Searchbar },

  data() {
    return {
      walletAddress: 0,

      contract: {},

      showSortingOptions: false,
      sortingOptions: [
        "unsorted",
        "rank Lo to Hi",
        "rank Hi to Lo",
        "number Lo to Hi",
        "number Hi to Lo",
      ],
      sorting: "unsorted",

      loading: true,
      nftsToShow: [],
      maxNftsToLoad: 40,
      nftsInWallet: [],
      nftInfo: [],
      searchString: "SEARCH FOR #ID",
    };
  },

  methods: {
    showPopup: function (_title, _text, _timeOut) {
      this.$refs.popup.show(_title, _text, _timeOut);
    },

    isScrolledIntoView(el) {
      const rect = el.getBoundingClientRect();
      const elemTop = rect.top;

      const preload = Math.max(200, window.innerHeight);

      const isVisible = elemTop < window.innerHeight + preload;
      return isVisible;
    },

    checkIfVisible() {
      const that = this;

      let scrolledTo = document.querySelector("#showMore");

      if (
        that.nftsToShow.length < that.nftsInWallet.length &&
        scrolledTo &&
        this.isScrolledIntoView(scrolledTo)
      ) {
        that.nftsToShow = that.nftsInWallet.slice(
          0,
          that.nftsToShow.length + that.maxNftsToLoad
        );
      }
    },

    loadMoreNftsIfScrolledDown() {
      // loads more NFTs when we scroll down
      const that = this;

      // works well if we don't have two halves side by side
      window.onscroll = () => {
        that.checkIfVisible();
      };

      // works well if on computer using scroll wheel
      window.onwheel = () => {
        that.checkIfVisible();
      };

      // works well if on computer using keyboard to scroll
      window.onkeydown = () => {
        that.checkIfVisible();
      };

      // works if on computer using touchscreen to scroll
      window.onmousemove = () => {
        that.checkIfVisible();
      };
    },

    searchNfts: function () {
      let _tokenId = this.searchString;

      if (!this.nftsInWallet.includes(_tokenId)) {
        //   this.showPopup(`NFT ${_tokenId}`, `is in your wallet`, 3000);
        // } else {
        this.showPopup(`NFT ${_tokenId}`, `is not in your wallet`, 3000);
        return;
      }

      this.nftsToShow = [_tokenId];
    },

    _sortNfts: function () {
      let that = this;

      switch (that.sorting) {
        case that.sortingOptions[0]:
          console.log("No sorting required");
          return false;

        case that.sortingOptions[1]:
          that.nftsInWallet.sort(function (a, b) {
            return that.nftInfo[a]["rank"] - that.nftInfo[b]["rank"] || a - b;
          });
          break;

        case that.sortingOptions[2]:
          that.nftsInWallet.sort(function (a, b) {
            return that.nftInfo[b]["rank"] - that.nftInfo[a]["rank"] || b - a;
          });
          break;

        case that.sortingOptions[3]:
          that.nftsInWallet.sort(function (a, b) {
            return a - b;
          });
          break;

        case that.sortingOptions[4]:
          that.nftsInWallet.sort(function (a, b) {
            return b - a;
          });
          break;

        default:
          that.feedbackMessage = "Sorting not implemented";
      }
    },

    sortNfts: function () {
      this._sortNfts();
      this.nftsToShow = this.nftsInWallet.slice(0, this.maxNftsToLoad);
    },

    // getRarity: function (_tokenId) {
    //   return window.rarity[tokenId - 1];
    // },

    getRank: function (_tokenId) {
      return window.rank[_tokenId - 1];
    },

    load: async function () {
      let that = this;

      that.loading = true;

      that.nftsToShow = [];
      that.nftsInWallet = [];

      that.walletAddress = await that.signer.getAddress();

      that.contract["nft"]
        .walletOfOwner(that.walletAddress)
        .then((_nftsInWallet) => {
          // for (let iCopy = 0; iCopy < _nftsInWallet.length; iCopy++) {
          // let _tokenId = Number(_nftsInWallet[iCopy]);
          for (let iCopy = 0; iCopy < 10000; iCopy++) {
            let _tokenId = iCopy + 1;

            // let _rarity = that.getRarity(_tokenId);
            let _rank = that.getRank(_tokenId);

            that.nftsInWallet.push(_tokenId);

            that.nftInfo[_tokenId] = {
              rank: _rank,
              image: window.pictureLink + _tokenId + ".png",
              // rarity: _rarity,
            };
          }
        })
        .then(() => {
          console.log("sorting NFTs");
          that.sortNfts();
          that.loading = false;
        });
    },

    setupContracts: function () {
      this.contract["nft"] = new ethers.Contract(
        window.address["nft"],
        KimonNFT.abi,
        this.signer
      );
    },
  },

  mounted: function () {
    this.loadMoreNftsIfScrolledDown();

    const that = this;

    that.provider = new ethers.providers.Web3Provider(window.ethereum, "any");

    that.provider
      .send("eth_requestAccounts", [])
      .then(() => {
        that.signer = that.provider.getSigner();
      })
      .then(() => {
        that.setupContracts();

        that.load();
      });
  },
};
</script>
  
<style lang="scss" scoped>
@import "@/assets/style.scss";
</style>